(function() {
  'use strict';

  angular
    .module('recAdmin', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'restangular', 'ui.router', 'ngMaterial', 'toastr', 'ng-token-auth', 'recAdmin.config']);

})();

(function() {
  'use strict';
  angular
    .module('recAdmin')
    .directive('recCalendar', recCalendar);

  function recCalendar($timeout) {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/dashboard/recCalendar/recCalendar.html',
      link: linkFunc,
      controller: recCalendarController,
      controllerAs: 'recCalendar'
    }

    return directive;

    function linkFunc(scope, el, attr, vm) {
      el.fullCalendar({
				header: {
					left: 'title',
          center: 'month,agendaWeek,agendaDay',
					right: 'prev,next'
				},
				defaultDate: moment().format('YYYY-M-D'),
        defaultView: 'agendaWeek',
				lang: 'es',
				buttonIcons: {
          prev: 'left-single-arrow',
          next: 'right-single-arrow'
        },
				editable: true,
				eventLimit: true,
				events: [
				]
			});
    }

    function recCalendarController() {
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('recAdmin')
    .controller('RegisterController', RegisterController);

    function RegisterController($scope, $state, $auth) {
       var vm = this;
       vm.doRegister = function() {
         $auth.submitRegistration($scope.registrationForm)
           .then(function(res){
             vm.res = res;
             $state.go('main');
           })
           .catch(function(err){
             var errors = err.data.errors
             vm.errors = errors.full_messages || errors
           })
       }

       vm.toLogin = function() {
         $state.go('login');
       }
    }
})();

(function() {
  'use strict';

  angular
    .module('recAdmin')
    .controller('LoginController', LoginController);

    function LoginController($scope, $state, $auth) {
       var vm = this
       vm.doLogin = function() {
         $auth.submitLogin($scope.loginForm)
           .then(function(res){
             vm.res = res;
             $state.go('dashboard');
           })
           .catch(function(err){
             vm.errors = err.errors;
           })
       }

       vm.toRegister = function() {
         $state.go('register');
       }
    }
})();

(function() {
  'use strict';
  angular
    .module('recAdmin')
    .controller('DashboardController', DashboardController);

    function DashboardController($scope, $state, $auth) {
       var vm = this;

       vm.entities = [
         {id: 1, name: 'Canchas'},
         {id: 2, name: 'Barrios'},
         {id: 3, name: 'Reservas'},
         {id: 4, name: 'Deportes'}
       ];

       vm.showContent = function(optionId) {
         switch (optionId) {
           case 1:
             console.log('canchas')
             break
           case 2:
             console.log('barrios')
             break
           case 3:
             $state.go('dashboard.reservations')
             break
           case 4:
             console.log('deportes')
             break
           default:
             console.error('invalid option')
         }
       }
    }
})();

